#About{
    width: 100%;
    height: 35px;
}
.app__headerBox{
    height: max-content;
    background: var(--deepBlue-color);
    @media screen and (min-width: 768px) {
        display: flex;
        flex-direction: column;
        height: max-content;
    }
}
.app__header{
    flex: 1;
    width: 100%;
    flex-direction: row;
    padding: 3rem 0rem 0em;
    @media screen and (max-width: 768px) {
        flex-direction: column;
    }
    @media screen and (min-width: 1200px) {
        padding:5rem 5rem 0em;
    }
    @media screen and (min-width: 2000px) {
        padding: 0rem 16rem 0em;
    }
    .app__header-info {
        display: flex;
        flex-direction: column;
        justify-content: flex-start;
        align-items: center;
        height: max-content;

        @media screen and (max-width:1200px) {
            width: 100%;
        }
    }
}
.app_header-icon{
    position: fixed;
    left: 0;
    bottom: 90px;
    z-index: 500;
    padding: 0rem 0rem 0rem 3rem;
    ul{
        list-style: none;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;

        li{
        padding: 3rem 0rem 0rem;
            
        a{
            color: var(--lightBlue-color);
            opacity: 0.7;
            font-size: 1.2rem;
            transition: all 300ms ease-in-out;
            position: relative;
            bottom: 0px;
            &:hover{
                bottom: 5px;
                color: var(--mainPurple-color);
            }
        }
        }
    }
    @media screen and (max-width: 1200px) {
        display: none;
    }
}
// .app__header-title{
//     padding-bottom: 5rem;
// }
.app_project-title,
.app_header-mobileText{
    // border: 1px solid red;
    width: 100%;
    height: max-content;
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;
    flex-direction: column;

    .header-title{
        padding: 1rem 0rem 0rem;
        font-family: 'Courier Prime', monospace;
        font-size: 1rem;
        display: flex;
        flex-direction: row;
        align-items: center;

        svg{
            color: var(--lightBlue-color);
            font-size: 0.8rem;
        }
        span{
            // font-size: 20px;
            padding-left: 1rem;
        }
        @media screen and (min-width: 1200px) {
            padding: 0rem 0rem 8rem;
        }
    }
    .header-content{
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        padding: 8rem 0rem 0rem 1.2rem;
        font-family: Roboto;
        @media screen and (min-width:1200px) {
            padding: 1rem 2rem 0rem 1.8rem;    
        }
        h1{
            font-size: 70px;
            font-weight: 700;
            color: var(--white-color);
            line-height: 110%;
            @media screen and (max-width: 1200px) {
                    font-size: 60px;
                }
            @media screen and (max-width: 480px) {
                font-size: 50px;
            }
        }
        h2{
            font-size: 65px;
            font-weight: 200;
            color: var(--lightBlue-color);
            line-height: 110%;
            padding-top: 1.5rem;
            @media screen and (max-width: 1200px) {
                    font-size: 50px;
                }
            @media screen and (max-width: 480px) {
                font-size: 40px;
            }
        }
        p{
            display: none;
            @media screen and (min-width:768px) {
                margin-top: 2rem;
                width: 90%;
                font-size: 1.2rem;
                font-weight: 200;
                line-height: 190%;
                color: var(--lightBlue-color);
                display: block;
            }
        }
    }
}

.app_header-img{
    display: none;
    @media screen and (min-width: 1200px) {
        display: flex;
        position: relative;
        padding: 0rem 1rem;
        max-width: 30%;
    }
    img{
        display: none;
        @media screen and (min-width: 1200px) {
            max-width: 100%;
            display: block;
            margin: 0px auto;
            object-fit: contain;
        }
        @media screen and (min-width: 2000px) {
            max-width: 65%;
        }
    }
}
.app_header-Deco{
    width: 120px;
    height: 120px;
    margin: 0px auto;
   
    margin: 10rem 0rem 6rem;
    @media screen and (min-width: 768px) {
        padding: 2rem 0rem 0rem;
    }
    @media screen and (min-width: 1200px) {
        display: none;
    }
    img{
        width: 100%;
        object-fit: contain;
        @media screen and (min-width: 780px) {
            display: none;
        }
    }
}
#app__header-mobile{
    flex: 1;
    width: 100%;
    flex-direction: row;
    background: var(--deepBlue-color);

    .app_header-mobileText{
        justify-content: center;
       .app_mobileText-img{
            padding: 5rem 0rem 0rem 1.5rem;
            max-width: 50%;
            img {
                max-width: 80%;
                object-fit: contain;
                @media screen and (min-width: 650px) {
                    max-width: 65%;
                }
            }
        }
        
        .header-content{
            height: max-content;
            padding: 2rem 0rem 0rem 1.8rem;
            p{
                margin-top: 0rem;
                width: 90%;
                font-size: 1rem;
                font-weight: 200;
                line-height: 190%;
                color: var(--lightBlue-color);
                display: block;
            }
        }
    }
    @media screen and (min-width: 768px) {
        display: none;
    }
}