.app__skills{
    padding: 2rem 0.3rem 0em;
    
    @media screen and (min-width: 1200px) {
        padding: 5rem 8rem 0em;
    }

    @media screen and (min-width: 2000px) {
        padding: 3rem 18rem 0em;
    }
}


.app__Skills-container{
    width: 80%;
    margin-top: 3rem;

    display: flex;
    flex-direction: row;

    @media screen and (max-width: 768px){
        width: 100%;
        flex-direction: column;
        align-items: center;
        
    }
}
.app__Skills-list{
    flex: 1;
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-start;
    align-items: flex-start;

    margin-right: 5rem;
    @media screen and (max-width: 900px) {
        margin-right: 0rem;
        justify-content: center;
        align-items: center;

    }
}
.app__skills-item{
    flex-direction: column;
    text-align: center;
    margin: 1rem;
    div{
        width: 100px;
        height: 100px;
        border-radius: 50%;
        background-color: #fff;
        img{
            width: 50%;
            // height: 50%;
        }
        @media screen and (min-width: 1200px) {
            width: 100px;
            height: 100px;
        }
        @media screen and (max-width: 1200px) {
            width: 70px;
            height: 70px;
        }
    }
    p{
        font-weight: 500;
        margin-top: 0.5rem;
    }
    @media screen and (min-width: 2000px) {
        margin-top: 1rem 2rem;
    }
}
.app__skills-exp{
    flex: 1;
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;
    flex-direction: column;
    @media screen and (max-width: 900px) {
        margin-top: 2rem;
    }
    
}
.app__skills-exp-item{
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: flex-start;
    margin: 1rem 0;
}
.app__skills-exp-works{
    flex: 1;

    .app__skills-exp-work{
        display: flex;
        flex-direction: column;
        justify-content: flex-start;
        align-items: flex-start;

        margin-bottom: 1rem;
        cursor: pointer;
        h4{
            font-weight: 400;

        }
        p{
            font-weight: 200;
            color: var(--lightBlue-color);
        }
    }
}
.app__skills-exp-year{
    margin-right: 3rem;

    p{
        font-weight: 800;
    }
    @media screen and (max-width: 450px) {
        margin-right: 1rem;
    }
}
.skills-tooltip{
    max-width: 300px !important;
    background-color: var(--white-color) !important;
    box-shadow: 0 0 25px rgba(0,0,0,0.1) !important;
    border-radius: 5px !important;
    padding: 1rem !important;
    color: var(--mainPurple-color) !important;
    text-align: center !important;
    line-height: 1.5 !important;
    opacity: 1 !important;

    @media screen and (min-width: 2000px) {
        font-size: 1.75rem !important;
        max-width: 500px !important;
        line-height: 2 !important;
    }
}